import React, { useState, useEffect } from 'react';
import { Text, View, Dimensions } from 'react-native';
import Icon from 'react-native-vector-icons/Ionicons';
import { SafeAreaView } from 'react-native-safe-area-context';
import { Emitter } from 'react-native-particles';
import { useFocusEffect } from '@react-navigation/native';

import style from '../styles/clickyStyle';

export default function ClickyScreen() {
    const [coins, setCoins] = useState(0);
    const [emitterLocation, setEmitterLocation] = useState(0);
    const [particles, setParticles] = useState(0);
    // const [globalCoinsToUpload, setGlobalCoinsToUpload] = useState(0);
    const [screenWidth, setScreenWidth] = useState(Math.round(Dimensions.get('window').width));

    function clickCoin() {
      window.globalCoinsToUpload = window.globalCoinsToUpload + 1;
      setEmitterLocation(Math.floor(Math.random() * screenWidth) + 1)
      setParticles(particles + 1)
      setCoins(coins + 1)
    }

    function uploadGlobalCoins() {
      if (window.globalCoinsToUpload > 0) {
        const CoinsUploading = window.globalCoinsToUpload;
        // console.log('Uploading Coins... '+CoinsUploading)
        fetch('https://antwanve.nl/globalclickergame/addCoin.php?coins='+CoinsUploading+'&key=19ASD9381NF23D432FGDA')
          .then(response => response.json())
          .then(data => {
            // console.log('Uploading Coins Success')
            window.globalCoinsToUpload = window.globalCoinsToUpload - CoinsUploading;
            loadGlobalCoins()
          })
        .catch(function (err) {
          console.log(err);
        });
      } else {
        loadGlobalCoins()
      }
      
    }

    function loadGlobalCoins() {
      // console.log('Loading Coins...')
      fetch('https://antwanve.nl/globalclickergame/getCoins.php?key=19ASD9381NF23D432FGDA')
        .then(response => response.json())
        .then(data => {
          // console.log('Loading Coins Success')
          setCoins(parseInt(data.coins) + window.globalCoinsToUpload);
          window.globalcoins = data.coins;
        })
      .catch(function (err) {
        console.log(err);
      });
    }
  
    useEffect(() => {
      setTimeout(() => {
        if (particles > 0) {
          setParticles(particles - 1)
        }
      }, 100);
    }, [particles]);

    useEffect(() => {
      window.globalCoinsToUpload = 0;
      loadGlobalCoins()
      setInterval(() => {
        uploadGlobalCoins()
      }, 10000);
    }, []);

    
  
    return (
      <SafeAreaView style={style.page}
        onStartShouldSetResponder={
          () => clickCoin()
        }
      >
  
        <Emitter
          numberOfParticles={particles}
          emissionRate={1}
          interval={1}
          particleLife={1000}
          direction={-90}
          spread={360}
          gravity={15}
          speed={10}
          fromPosition={{ x: emitterLocation - 50, y: 1 }}
          infiniteLoop
        >
          <Icon name={'ios-disc'} size={50} color={'yellow'} />
        </Emitter>
  
        <View style={style.counter}>
          <Text style={style.counterText}>{coins}</Text>
          <Icon name={'ios-disc'} size={50} color={'yellow'} />
        </View>
  
      </SafeAreaView>
    );
  }